<template>
  <div class="user">
    <administrator v-if="flag === '管理员'"></administrator>
    <super-admin v-if="flag === '超级管理员'"></super-admin>
  </div>
</template>

<script>
import Administrator from 'components/administrator/administrator'
import SuperAdmin from 'components/superAdmin/superAdmin'
import {mapGetters} from 'vuex'

export default {
  data () {
    return {
      flag: ''
    }
  },
  created () {
    this.flag = this.role === 'ROLE_USER' ? '管理员' : '超级管理员'
  },
  components: {
    Administrator,
    SuperAdmin
  },
  computed: {
    ...mapGetters([
      'role',
      'user'
    ])
  }
}
</script>

<style lang="scss">
@import "common/css/variable.scss";

.user {
  color: $color-theme;
}
</style>



