<template>
  <div class="superAdmin">
    <h1>用户管理</h1>
    <div class="search">
      <div>
        用户名称:
        <input type="text" v-model="name" placeholder="请输入用户名" class="text" autocomplete="off"/>
      </div>
      <div>
        状态:
        <select v-model="state">
          <option value>请选择</option>
          <option value="1">可用</option>
          <option value="2">禁用</option>
        </select>
      </div>
    </div>
    <el-button class="create" @click="addDialogVisible = true">新建</el-button>

      <!-- 添加用户 -->
      <el-dialog
        title="添加用户"
        :visible.sync="addDialogVisible"
        width="50%"
        top="100px"
        :before-close="handleClose">
        <user-form ref="addUser" @success="successReload"></user-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="postUser">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 修改用户 -->
      <el-dialog
        title="修改用户"
        :visible.sync="changeDialogVisible"
        width="50%"
        top="100px"
        :before-close="handleClose">
        <user-form ref="changeUser" :alterId="alterId" :form="form" :showPwd="showPwd" @alter="alterReload"></user-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="changeDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="postAlterUser">确 定</el-button>
        </span>
      </el-dialog>

    <button class="edit" @click="showDel=!showDel">编辑</button>
    <button class="edit del" v-show="showDel" @click="delMore">删除</button>
    <div class="table">
      <el-table :data="filterName(userList)" border style="width:100%" @select="selectDel" @select-all="selectAllDel" @cell-click="banUser">
        <el-table-column align="center" type="selection" v-if="showDel"></el-table-column>
        <el-table-column prop="id" label="编号"></el-table-column>
        <el-table-column prop="username" label="用户名"></el-table-column>
        <el-table-column prop="state" label="状态" class-name="userState">
          <template slot-scope="scope">
            <i :class="scope.row.state | aliasState"></i>
          </template>
        </el-table-column>
        <el-table-column prop="roles" label="权限"></el-table-column>
        <el-table-column prop="address" label="地址"></el-table-column>
        <el-table-column prop="email" label="邮箱" width="200"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="deleteById(scope.row.id)">删除</el-button>
            <el-button type="text" size="small" @click="alterUser(scope.row.id)">修改</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        class="page"
        :total="total"
        :page-size="pageSize"
        @current-change="currentChange"
        @prev-click="prevClick"
        @next-click="nextClick"
      ></el-pagination>
    </div>
     <div class="loading" v-show="!userList.length>0">
      <loading title="拼命加载中..."></loading>
    </div>
  </div>
</template>

<script>
import {getAllUser, delUser, delMoreUser, queryUserById, banUser} from 'api/user'
import UserForm from 'components/userForm/userForm'
import Loading from 'components/loading/loading'

const TABLETOP = 278

export default {
  created () {
    this.delUserList = [] // 批量删除用户列表
    this.pageSize = parseInt((document.documentElement.clientHeight - TABLETOP) / 70)
    this.getAllUser()
    this.tempList = []
  },
  data () {
    return {
      showDel: false, // 显示隐藏编辑列
      page: 1, // 页码
      pageSize: 6, // 每页数量
      total: 0, // 数据总数
      userList: [], // 当页用户列表
      addDialogVisible: false, // 添加用户弹框
      changeDialogVisible: false, // 修改用户弹框
      form: '', // 表单
      showPwd: false, // 隐藏修改密码项
      alterId: 0, // 修改用户的id
      name: '', // 搜索框姓名
      state: 0 // 搜索框状态
    }
  },
  methods: {
    filterName (list) {
      let temp = []
      list.forEach(item => {
        if (item.username.includes(this.name)) {
          temp.push(item)
        }
      })
      return temp
    },
    banUser (row, column, cell) {
      if (cell.firstChild.firstChild.tagName === 'I') {
        banUser(row.id).then(res => {
          this.getAllUser()
        })
      }
    },
    alterReload () {
      this.getAllUser()
      this.changeDialogVisible = false
    },
    postAlterUser () {
      this.$refs.changeUser.alterSubmit('修改')
    },
    alterUser (id) {
      this.alterId = id
      queryUserById(id).then(res => {
        this.form = {
          username: res.data.username,
          address: res.data.address,
          phone: res.data.phone,
          email: res.data.email
        }
        this.changeDialogVisible = true
      })
    },
    postUser () {
      this.$refs.addUser.onSubmit('上传')
    },
    successReload () {
      this.page = this.total
      this.getAllUser()
      this.addDialogVisible = false
    },
    handleClose (done) {
      done()
    },
    getAllUser () {
      getAllUser(this.page, this.pageSize).then(res => {
        this.userList = res.data.list
        this.total = res.data.total
        this.page = res.data.pageNum
        this.tempList = this.userList
      })
    },
    deleteById (id) {
      this.$confirm('此操作将永久删除用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUser(id).then(res => {
          this.getAllUser()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          offset: 240
        })
      })
    },
    delMore () {
      let temp = []
      this.delUserList.forEach(item => {
        temp.push(item.id)
      })
      this.$confirm(`是否批量删除${this.delUserList.length}个用户`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delMoreUser({ids: JSON.stringify(temp)}).then(res => {
          this.getAllUser()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          offset: 240
        })
      })
    },
    // 获取当前页码
    currentChange (currentPage) {
      this.page = currentPage
      this.getAllUser()
    },
    // 上一页
    prevClick (currentPage) {
      this.page = currentPage
      this.getAllUser()
    },
    // 下一页
    nextClick (currentPage) {
      this.page = currentPage
      this.getAllUser()
      this.state = ''
    },
    // 批量删除
    selectDel (selection, row) {
      this.delUserList = selection
    },
    selectAllDel (selection) {
      this.delUserList = selection
    }
  },
  components: {
    UserForm,
    Loading
  },
  filters: {
    aliasState (msg) {
      return msg ? 'el-icon-unlock unlock' : 'el-icon-lock lock'
    }
  },
  watch: {
    state (newState) {
      let temp = []
      if (newState === '1') {
        this.tempList.map(item => {
          if (item.state === true) {
            temp.push(item)
          }
          this.userList = temp
        })
      } else if (newState === '2') {
        this.tempList.map(item => {
          if (item.state === false) {
            temp.push(item)
          }
        })
        this.userList = temp
      } else {
        this.userList = this.tempList
      }
    }
  }
}
</script>

<style lang="scss">
@import "common/css/variable.scss";
.superAdmin {
  margin: 30px 0 0 30px;
  color: $color-theme;
  position: relative;
  .search {
    margin-top: 25px;
    margin-right: 15px;
    height: 50px;
    background-color: $color-highlight-background;
    line-height: 50px;
    color: $color-theme;
    display: flex;
    justify-content: space-around;
    padding-right: 500px;
    .text {
      height: 22px;
      font-size: 12px;
      box-sizing: border-box;
      padding-left: 12px;
      &::placeholder {
        color: #666;
      }
      color: #333;

    }
    .major {
      position: relative;
    }
    .tip {
      position: absolute;
      margin-top: 5px;
      border-radius: 10px;
      overflow: hidden;
      transition: all 0.3s;
      background-color: $color-highlight-background;
      z-index: 20;
      width: 155px;
      top: 40px;
      left: 74px;
      li {
        padding: 10px 15px;
        height: 16px;
        line-height: 16px;
      }
      li:last-child {
        margin-bottom: 5px;
      }
      li:hover {
        cursor: pointer;
        background-color: #222;
      }
    }
  }
  .create {
    margin: 20px 0;
    background-color: $color-theme-d;
    color: #fff;
  }
  .edit {
    background-color: $color-theme-d;
    color: $color-theme;
    background-color: transparent;
    border-radius: 10px;
    width: 60px;
    height: 30px;
    position: absolute;
    outline-color: $color-theme;
    top: 155px;
    right: 40px;
    border: 1px solid $color-theme-d;
    cursor: pointer;
  }
  .del {
    right: 120px;
  }
  .table {
    .userState {
    cursor: pointer;
   }
    margin-right: 15px;
    .el-checkbox__inner {
      border-radius: 50%;
      background-color: transparent;
      border: 1px solid $color-theme;
    }
    .el-table--enable-row-hover .el-table__body tr:hover>td{
    background-color: #333 !important;
    }

    .page {
      margin-top: 8px;
      text-align: center;
      .el-dialog,
      .el-pager li {
        color: $color-theme;
        background-color: $color-background;
      }
      .el-pager li.active {
        color: #fff;
      }
    }
    .el-pagination button {
      color: #fff;
      background-color: $color-background;
    }
    .el-table th,
    .el-table tr {
      background-color: $color-background;
      color: $color-theme;
    }
    .el-table th.gutter {
      display: table-cell !important;
    }
    .el-button--text {
      color: $color-theme;
    }
  }
  .loading {
    width: 100px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50px);
  }
  .unlock,.lock{
    font-size: 16px;
  }
  .lock {
    color: $color-theme-d;
  }
  .loading {
      width: 100px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50px);
  }
}
</style>



