<template>
  <div class="main" :style="height">
    <!-- 导航条 -->
    <nav-com></nav-com>
    <!-- 侧边栏 -->
    <aside-com></aside-com>
    <div class="mask" v-show="flag">
      <div class="text">
        <div class="sun"><i class="el-icon-sunny"></i></div>
        <p>欢迎使用校园宽带管理系统!</p>
        <p>祝您使用愉快！</p>
      </div>
    </div>
    <!-- 主内容区域 -->
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import nav from 'components/nav/nav'
import aside from 'components/aside/aside'
import { getLoginMessage } from 'api/login'
import { mapGetters, mapActions } from 'vuex'

export default {
  data () {
    return {
      flag: false
    }
  },
  created () {
    if (this.loginState) {
      getLoginMessage().then(
        res => {
          if (res.data.roles) {
            this.saveUserAndRole(res.data)
          } else {
            console.log('请先登录')
            this.$router.push('/login')
          }
        },
        err => {
          console.log(err)
          this.$router.push('/login')
        }
      )
    } else {
      this.$router.push('/login')
    }
    if (this.$route.path === '/main') {
      this.flag = true
    }
  },
  methods: {
    ...mapActions(['saveUserAndRole'])
  },
  components: {
    NavCom: nav,
    asideCom: aside
  },
  computed: {
    height () {
      return `height: ${document.documentElement.clientHeight}px`
    },
    ...mapGetters(['loginState'])
  },
  watch: {
    '$route.path': function (newVal, oldVal) {
      if (newVal === '/main') {
        this.flag = true
      } else {
        this.flag = false
      }
    }
  },
  beforeRouterUpdate (to, from, next) {
    console.log(to)
  }

}
</script>

<style lang="scss" scoped>
@import "common/css/variable.scss";

.main {
  background-color: $color-background;
  height: 800px;
  overflow: hidden;
  .mask {
    position: absolute;
    left: 50%;
    // transform: translateX(-100px);
    .text {
      width: 400px;
      color: $color-theme;
      text-align: center;
      margin: 200px auto;
      position: relative;
      p {
        margin: 10px 0;
        font-size: 32px;
      }
      .sun {
        position: absolute;
        left: -130px;
        top: -20px;
        font-size: 80px;
      }
    }
  }
  .content {
    padding-left: 240px;
    z-index: 20;
  }
}
</style>



