<template>
  <div>
    <div class="nav">
      <h1 class="title">校园宽带管理系统 v0.1</h1>
      <router-link to="/main" class="index"><i class="el-icon-back"></i> 首页</router-link>
      <div class="dropdown">
        <p>{{str}}</p>
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">
            <i class="el-icon-user-solid"></i>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="1">个人中心</el-dropdown-item>
            <el-dropdown-item command="2">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown>
          <span class="el-dropdown-link">
            <i class="el-icon-message"></i>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>界面设计-庄林鑫</el-dropdown-item>
            <el-dropdown-item>后台设计-李灿鸿</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
import {exitLogin} from 'api/login'
export default {
  computed: {
    str () {
      return this.role === 'ROLE_USER' ? `管理员${this.user.username} , 欢迎您的到来！` : `超级管理员${this.user.username} , 欢迎您的到来！`
    },
    ...mapGetters([
      'user',
      'role'
    ])
  },
  methods: {
    handleCommand (command) {
      if (command === '2') {
        console.log('退出登录')
        exitLogin().then(res => {
          console.log(res.data)
        })
        this.$router.push('/login')
      }
      if (command === '1') {
        this.$router.push('/main/your')
      }
    }
  }
}
</script>

<style lang="scss">
@import "common/css/variable.scss";

.nav {
  height: 50px;
  background-color: $color-background-d;
  display: flex;
  justify-content: space-between;
  .index {
    position: absolute;
    text-decoration: none;
    color: $color-theme-d;
    font-size: 14px;
    left: 20%;
    line-height: 50px;
  }
  .title {
    margin-left: 24px;
    color: $color-theme-d;
    font-size: $font-size-large;
    display: flex;
    width: 200px;
    height: 100%;
    align-items: center;
  }
  .dropdown {
    display: flex;
    p {
      margin: 0;
      margin-right: 80px;
      color: #fff;
      height: 100%;
      line-height: 50px;
    }
    line-height: 50px;
    margin-right: 30px;
    height: 50px;
    .el-dropdown {
      color: $color-text;
      margin-right: 30px;
    }
  }
}
</style>



