<template>
  <div>
    <el-form ref="form" label-width="80px">
      <el-form-item label="用户名称*">
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" v-if="showPwd">
        <el-input v-model="form.password" type="password"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" v-if="showPwd">
        <el-input v-model="confirmPwd" type="password"></el-input>
      </el-form-item>
      <el-form-item label="邮箱*">
        <el-input  v-model="form.email"></el-input >
      </el-form-item>
      <el-form-item label="地址*">
        <el-input type="textarea" v-model="form.address"></el-input >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {postUser, alterUser} from 'api/user'

export default {
  data () {
    return {
      confirmPwd: ''
    }
  },
  props: {
    form: {
      type: Object,
      default: () => {
        return {
          username: '',
          password: '',
          address: '',
          email: '',
          phone: ''
        }
      }
    },
    showPwd: {
      type: Boolean,
      default: true
    },
    alterId: {
      type: Number,
      default: -1
    }

  },
  methods: {
    onSubmit (title) {
      const flag = this.check()
      if (flag) {
        this.$message({
          message: flag,
          offset: 500,
          type: 'info'
        })
        return
      }
      postUser(this.form).then(res => {
        this.$message({
          message: title + '成功',
          offset: 220,
          type: 'success'
        })
        this.$emit('success')
      // eslint-disable-next-line handle-callback-err
      }, err => {
        this.$message({
          message: title + '失败',
          offset: 220,
          type: 'warning'
        })
      })
    },
    alterSubmit (title) {
      const flag = this.check()
      if (flag) {
        this.$message({
          message: flag,
          offset: 580,
          type: 'info'
        })
        return
      }
      alterUser(this.alterId, this.form).then(res => {
        this.$message({
          message: title + '成功',
          offset: 220,
          type: 'success'
        })
        this.$emit('alter')
      // eslint-disable-next-line handle-callback-err
      }, err => {
        this.$message({
          message: title + '失败',
          offset: 220,
          type: 'warning'
        })
      })
    },
    check () {
      if (this.form.username === '') {
        return '请输入用户名称'
      }
      if (this.showPwd) {
        if (this.form.password === '') {
          return '请输入密码'
        }
        if (this.confirmPwd === '') {
          return '请输入确认密码'
        }
        if (this.confirmPwd !== this.form.password) {
          return '两次输入密码不一致'
        }
      }
      if (this.form.email === '') {
        return '请输入邮箱'
      }
      if (this.form.address === '') {
        return '请输入地址'
      }
      return false
    }
  }
}
</script>

<style lang="scss">
</style>



