import axios from 'axios'

// 合并后的地址路径
// const baseUrl = 'http://localhost:8080'
const baseUrl = window.location.origin

// 生产环境跨域测试
// const baseUrl = 'api'

// 获取所有客户列表 页码 每页数量 姓名 专业 级别
export function getCustomer (pageNum, pageSize, name, major, level) {
  const url = baseUrl + '/customer'
  const data = Object.assign({}, {
    pageNum,
    pageSize,
    level,
    name,
    major
  })
  return axios.get(url, { params: data })
}

// 获取所有专业信息
export function getMajor () {
  const url = baseUrl + '/major'
  return axios.get(url)
}
// 获取所有级别信息
export function getLevel () {
  const url = baseUrl + '/level'
  return axios.get(url)
}
// 根据id删除客户
export function delCustomer (delId) {
  const url = baseUrl + `/customer/${parseInt(delId)}`
  return axios.delete(url)
}
// 根据id查询指定客户
export function queryCustomerById (Id) {
  const url = baseUrl + `/customer/${parseInt(Id)}`
  return axios.get(url)
}
// 上传客户
export function postCustomer (form) {
  const url = baseUrl + '/customer'
  return axios.post(url, form)
}
// 修改客户
export function alterCustomer (Id, form) {
  const url = baseUrl + `/customer/${parseInt(Id)}`
  return axios.put(url, form)
}



