<template>
  <div class="administrator">
    <h1 class="title"><i class="el-icon-user"></i>用户个人资料</h1>
    <span class="edit" @click="changeDisabled"><i class="el-icon-edit"></i>编辑</span>
    <div class="box" ref="box">
      <div class="message">
        <div class="subtitle">username :</div>
        <input type="text" disabled  name="username" :value="user.username">
      </div>
      <div class="message">
        <div class="subtitle">roles :</div>
        <input type="text" disabled name="roles" :value="role | roleAlias">
      </div>
      <div class="message">
        <div class="subtitle">address :</div>
        <input type="text" disabled  name="address" v-model="form.address" autocomplete="off">
      </div>
      <div class="message">
        <div class="subtitle">email :</div>
        <input type="text" disabled  name="email" v-model="form.email" autocomplete="off">
      </div>
      <button v-show="flag" @click="alterUser">保存</button>
      <button v-show="flag" class="cancle" @click="cancle">取消</button>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import {alterCurrentUser} from 'api/user'
import {getLoginMessage} from 'api/login'

export default {
  created () {
    this.form.address = this.user.address
    this.form.email = this.user.email
  },
  data () {
    return {
      flag: false,
      form: {
        address: '',
        email: ''
      }
    }
  },
  methods: {
    changeDisabled () {
      document.querySelectorAll('.message input').forEach((item, index) => {
        if (item.disabled) {
          if (item.name === 'roles' || item.name === 'username') {
            return
          }
          item.removeAttribute('disabled')
          if (index === 2) {
            item.focus()
          }
          this.flag = true
        } else {
          item.disabled = 'disabled'
          this.flag = false
        }
      })
    },
    alterUser () {
      alterCurrentUser(this.form).then(res => {
        getLoginMessage().then(res => {
          this.$message({
            message: '修改成功',
            offset: 380,
            type: 'success'
          })
          this.saveUserAndRole(res.data)
        })
      })
    },
    cancle () {},
    ...mapActions([
      'saveUserAndRole'
    ])
  },
  computed: {
    ...mapGetters([
      'user',
      'role'
    ])
  },
  filters: {
    roleAlias (msg) {
      return msg === 'ROLE_USER' ? '管理员' : '超级管理员'
    },
    stateAlias (msg) {
      return msg ? '可用' : '禁用'
    }
  }
}
</script>

<style lang="scss">
@import "common/css/variable.scss";

.administrator {
  margin: 0 50px 0;
  color: $color-theme;
  position: relative;
  .edit {
    font-size: 28px;
    bottom: $color-theme;
    position: absolute;
    top: 5px;
    right: 50px;
    cursor: pointer;
    i {
      margin-right: 24px;
    }
  }
  .title {
    margin: 30px 0;
    i {
      margin-right: 24px;
    }
  }
  .box {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 50px 50px 50px 90px;
    .message {
      margin-top: 40px;
      position: relative;
      .subtitle {
        display: inline-block;
        text-align: right;
        width: 90px;
        // position: absolute;
        // left: -90px;
      }
      input {
        border: none;
        width: 300px;
        background-color: $color-background;
        border-bottom: 1px solid $color-theme;
        outline: none;
        font-size: 18px;
        caret-color: $color-theme;
        box-sizing: border-box;
        vertical-align: middle;
        padding-left: 12px;
        color: #fff;
      }
    }
    button {
      position: absolute;
      width: 54px;
      height: 28px;
      border-radius: 5px;
      border: 1px solid $color-theme-d;
      background-color: transparent;
      color: $color-theme;
      bottom: -30px;
      left: 15%;
      cursor: pointer;
      outline-color: $color-theme;
    }
    .cancle {
      left: 20%;
    }
  }
}
</style>



