// 节流函数
export function debounce (func, delay) {
  let timer
  // watch(newVal, oldVal)
  return function (...args) {
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(() => {
      func.apply(this, args)
    }, delay)
  }
}

const LOGIN_STATE = '__loginState__'
const LOGIN_ROLE = '__loginRole__'
const LOGIN_USER = '__loginUser__'
export function saveStorageState (state) {
  sessionStorage.setItem(LOGIN_STATE, state)
}

export function getStorageState () {
  return sessionStorage.getItem(LOGIN_STATE)
}

export function saveStorageRole (role) {
  sessionStorage.setItem(LOGIN_ROLE, role)
}

export function getStorageRole () {
  return sessionStorage.getItem(LOGIN_ROLE)
}

export function getStorageUser () {
  let str = sessionStorage.getItem(LOGIN_USER)
  return JSON.parse(str)
}

export function saveStorageUser (user) {
  user = JSON.stringify(user)
  sessionStorage.setItem(LOGIN_USER, user)
}



