<template>
  <div class="login" :style="setHeight">
      <h1>Welcome to the campus broadband management system.</h1>
      <div class="box">
        <div class="control">
          <i class="icon el-icon-user"></i>
          <input type="text" class="input" v-model="username" />
        </div>
        <div class="control">
          <i class="icon el-icon-user-solid"></i>
          <input type="password" ref="pwd" v-model="password" class="input" @blur="blur" @focus="showPwd = true" />
          <i class="eye el-icon-view" :style="pwdIcon" @click="showPassword" v-show="showPwd"></i>
        </div>
        <transition name="my">
          <p class="tip" v-show="flag">登录失败</p>
        </transition>
        <div class="control">
          <button class="btn" @click="login">登录</button>
        </div>
      </div>
  </div>
</template>

<script>
import {login} from 'api/login'
import {mapActions, mapGetters} from 'vuex'
export default {
  data () {
    return {
      pwdIcon: '',
      showPwd: false,
      show: '',
      username: '',
      password: '',
      flag: false
    }
  },
  computed: {
    setHeight () {
      return `height:${document.documentElement.clientHeight}px`
    },
    ...mapGetters([
      'loginState'
    ])
  },
  methods: {
    blur () {
      setTimeout(() => {
        this.showPwd = false
      }, 150)
    },
    showPassword () {
      if (this.$refs.pwd.type === 'password') {
        this.$refs.pwd.type = 'text'
        this.pwdIcon = 'color: #ffcd32'
      } else {
        this.$refs.pwd.type = 'password'
        this.pwdIcon = 'color: rgba(255, 205, 49, 0.5)'
      }
    },
    login () {
      login({'username': this.username, 'password': this.password}).then(res => {
        if (res.data && res.data.message) {
          this.saveLoginState(true)
          this.$router.push('/main')
          this.flag = false
        } else {
          this.flag = true
        }
      // eslint-disable-next-line handle-callback-err
      }, err => {
        this.flag = true
      })
    },
    ...mapActions([
      'saveLoginState'
    ])
  }
}
</script>
<style lang="scss">
@import "common/css/variable.scss";
.login {
  h1 {
    width: 600px;
    color: #fff;
    text-align: center;
    font-size: 36px;
    color: $color-theme;
    margin: 50px auto 0;
  }
  background-color: $color-highlight-background;
  overflow: hidden;
  .box {
    width: 400px;
    margin: 100px auto;
    position: relative;
    .control {
      margin-top: 50px;
      color: $color-theme;
      text-align: center;
      position: relative;
      .icon {
        margin-right: 15px;
        margin-top: 10px;
      }
      .input {
        background-color: $color-highlight-background;
        width: 200px;
        border: none;
        border-bottom: 1px solid $color-theme;
        outline: 0;
        caret-color: $color-theme;
        color: #fff;
      }
      .btn {
        border: none;
        background-color: transparent;
        border: solid 1px $color-theme-d;
        border-radius: 5px;
        width: 50px;
        height: 30px;
        color: $color-theme;
        outline: 0;
        outline-color: $color-theme;
        cursor: pointer;
      }
      .eye {
        position: absolute;
        top: 10px;
        right: 55px;
        color: $color-theme-d;
        cursor: pointer;
      }
    }
    .tip {
      position: absolute;
      color: $color-theme-d;
      bottom: 30px;
      left: 50%;
      transform: translateX(-50%)
    }
  }
}
  .my-enter,.my-leave-to {
    opacity: 0;
  }
  .my-enter-active,.my-leave-active {
    opacity: 1;
    transition: all 1s;
  }
</style>



