import * as types from './mutation-types'

const mutations = {
  [types.SET_LOGIN_STATE] (state, loginState) {
    state.loginState = loginState
  },
  [types.SET_ROLE] (state, role) {
    state.role = role
  },
  [types.SET_USER] (state, user) {
    state.user = user
  }
}

export default mutations



