<template>
  <div class="order">
    <div class="wait">
      {{msg}}
    </div>
  </div>
</template>

<script>
export default {
  created () {
    this.move()
  },
  data () {
    return {
      msg: '此功能待开发中。。。'
    }
  },
  methods: {
    move () {
      setInterval(() => {
        let temp = this.msg
        this.msg = temp.slice(0, temp.length - 1)
        this.msg = temp.slice(temp.length - 1) + this.msg
      }, 1000)
    }
  }
}
</script>
<style lang="scss">
@import "common/css/variable.scss";
.order {
  color: $color-theme;
  width: 100%;
  height: 100%;
  .wait {
    width: 600px;
    height: 300px;
    bottom: 0;
    top: 0;
    left: 100px;
    right: 0;
    margin: auto;
    font-size: 50px;
    position: absolute;
  }
}
</style>



