import axios from 'axios'

// 合并后的地址路径
// const baseUrl = 'http://localhost:8080'

const baseUrl = window.location.origin
// 生产环境跨域测试
// const baseUrl = 'api'

// 修改当前登录用户
export function alterCurrentUser (message) {
  const url = baseUrl + '/active_user.message'
  return axios.put(url, message)
}

// 查看所有用户(超级管理员)
export function getAllUser (pageNum, pageSize) {
  const url = baseUrl + '/user'
  const data = Object.assign({}, {
    pageNum,
    pageSize
  })
  return axios.get(url, { params: data })
}

// 删除用户(超级管理员)
export function delUser (id) {
  const url = baseUrl + `/user/${parseInt(id)}`
  return axios.delete(url)
}

// 批量删除用户(超级管理员)
export function delMoreUser (users) {
  const url = baseUrl + `/user/batchdelete`
  // return axios.post(url, users)
  return axios({
    url,
    method: 'post',
    data: users,
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    }
  })
}

// 上传用户(超级管理员)
export function postUser (user) {
  const url = baseUrl + `/user`
  return axios.post(url, user)
}

// 修改用户(超级管理员)
export function alterUser (id, user) {
  const url = baseUrl + `/user/${parseInt(id)}`
  return axios.put(url, user)
}

// 根据id查询用户(超级管理员)
export function queryUserById (id) {
  const url = baseUrl + `/user/${parseInt(id)}`
  return axios.get(url)
}

// 禁用用户(超级管理员)
export function banUser (id) {
  const url = baseUrl + `/user/${parseInt(id)}`
  return axios.patch(url)
}



