import {getStorageState, getStorageRole, getStorageUser} from 'common/js/util'

const store = {
  loginState: getStorageState() || false, // 登录状态
  role: getStorageRole() || '刷新后重试', // 登录角色
  user: getStorageUser() || {username: ''} // 登录用户信息
}

export default store



