<template>
  <div>
    <el-form ref="form" label-width="80px">
      <el-form-item label="客户名称*">
        <el-input v-model="form.cust_name"></el-input>
      </el-form-item>
      <el-form-item label="专业*">
        <el-select v-model="form.cust_major" placeholder="请选择专业">
          <el-option :label="item" :value="item" v-for="item in majorList" :key="item"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="级别*">
        <el-radio-group v-model="form.cust_level">
          <el-radio :label="item" v-for="item in levelList" :key="item"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="联系人*">
        <el-input v-model="form.cust_linkman"></el-input >
      </el-form-item>
      <el-form-item label="联系电话*">
        <el-input type="number" v-model="form.cust_mobile"></el-input >
      </el-form-item>
      <el-form-item label="地址*">
        <el-input type="textarea" v-model="form.cust_address"></el-input >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {postCustomer, alterCustomer} from 'api/customer'

export default {
  props: {
    majorList: {
      type: Array,
      default: () => []
    },
    levelList: {
      type: Array,
      default: () => []
    },
    form: {
      type: Object,
      default: () => {
        return {
          cust_name: '',
          cust_major: '',
          cust_level: '',
          cust_linkman: '',
          cust_mobile: '',
          cust_address: '',
          cust_createtime: '',
          cust_create_id: '',
          cust_phone: '',
          cust_zipcode: '',
          cust_id: ''
        }
      }
    }
  },
  methods: {
    onSubmit (title) {
      const flag = this.check()
      if (flag) {
        this.$message({
          message: flag,
          offset: 580,
          type: 'info'
        })
        return
      }
      this.form.cust_createtime = new Date()
      postCustomer(this.form).then(res => {
        this.$message({
          message: title + '成功',
          offset: 220,
          type: 'success'
        })
        this.$emit('success')
      // eslint-disable-next-line handle-callback-err
      }, err => {
        this.$message({
          message: title + '失败',
          offset: 220,
          type: 'warning'
        })
      })
    },
    alterSubmit (title) {
      const flag = this.check()
      if (flag) {
        this.$message({
          message: flag,
          offset: 580,
          type: 'info'
        })
        return
      }
      this.form.cust_createtime = new Date()
      alterCustomer(this.form.cust_id, this.form).then(res => {
        this.$message({
          message: title + '成功',
          offset: 220,
          type: 'success'
        })
        this.$emit('alter')
      // eslint-disable-next-line handle-callback-err
      }, err => {
        this.$message({
          message: title + '失败',
          offset: 220,
          type: 'warning'
        })
      })
    },
    check () {
      if (this.form.cust_name === '') {
        return '请输入客户名称'
      }
      if (this.form.cust_major === '') {
        return '请选择专业'
      }
      if (this.form.cust_level === '') {
        return '请选择级别'
      }
      if (this.form.cust_linkman === '') {
        return '请输入联系人'
      }
      if (this.form.cust_mobile === '') {
        return '请输入联系电话'
      }
      if (this.form.cust_address === '') {
        return '请输入地址'
      }
      return false
    }
  }
}
</script>

<style lang="scss">
</style>



