import * as types from './mutation-types'
import * as util from 'common/js/util'

export const saveLoginState = function ({commit, state}, loginState) {
  commit(types.SET_LOGIN_STATE, loginState)
  util.saveStorageState(loginState)
}

export const saveUserAndRole = function ({commit, state}, user) {
  commit(types.SET_ROLE, user.roles)
  util.saveStorageRole(user.roles)
  const users = {
    id: user.id,
    username: user.username,
    state: user.state,
    address: user.address,
    email: user.email
  }
  util.saveStorageUser(users)
  commit(types.SET_USER, users)
}



