<template>
  <div class="customer">
    <h1>客户管理</h1>
    <!-- 顶部搜索栏 -->
    <div class="search">
      <div>客户名称: <input type="text" name="name" v-model="name" autocomplete="off"></div>
      <div class="major">
        所属专业: <input type="text" name="major" v-model="major" @focus="focus" @blur="blur" autocomplete="off">
            <ul class="tip" :style="showTip" ref="tip">
              <li v-for="item in filterMajor(majorList)" :key="item" @click.stop="major=item">{{item}}</li>
            </ul>
      </div>
      <div>
          客户级别:
        <select name="level" v-model="level" @change="selectLevel">
          <option value="">请选择</option>
          <option :value="item" v-for="item in levelList" :key="item">{{item}}</option>
        </select>
      </div>
    </div>
    <el-button class="create" @click="addDialogVisible = true">新建</el-button>
    <!-- 添加客户弹框 -->
      <el-dialog
        title="添加客户"
        :visible.sync="addDialogVisible"
        width="50%"
        top="100px"
        :before-close="handleClose">
        <customer-form :majorList="majorList" :levelList="levelList" @success="successReload" ref="addCustomerForm"></customer-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="postCustomer">确 定</el-button>
        </span>
      </el-dialog>
    <!-- 修改客户弹框 -->
       <el-dialog
        title="修改客户"
        :visible.sync="changeDialogVisible"
        width="50%"
        top="100px"
        :before-close="handleClose">
        <customer-form :majorList="majorList" :levelList="levelList" @alter="alterReload" ref="alterCustomerForm" :form="form"></customer-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="changeDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="postAlterCustomer">确 定</el-button>
        </span>
      </el-dialog>
    <!-- 客户表格 -->
    <div class="table" v-show="customerList.length>0">
        <el-table :data="customerList" border style="width:100%">
            <el-table-column prop="cust_id" label="编号" sortable></el-table-column>
            <el-table-column prop="cust_name" label="姓名"></el-table-column>
            <el-table-column prop="cust_level" label="级别" ></el-table-column>
            <el-table-column prop="cust_linkman" label="联络人" ></el-table-column>
            <el-table-column prop="cust_major" label="专业" ></el-table-column>
            <el-table-column prop="cust_mobile" label="手机" ></el-table-column>
            <el-table-column prop="cust_createtime" :formatter="dateFormat" label="办理日期" sortable ></el-table-column>
            <el-table-column  label="操作">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="deleteById(scope.row.cust_id)">删除</el-button>
                <el-button type="text" size="small" @click="alterCustomer(scope.row.cust_id)">编辑</el-button>
              </template>
            </el-table-column>
        </el-table>
       <el-pagination
       layout="prev, pager, next"
       :page-size="pageSize"
       :total="total"
       class="page"
       @current-change="currentChange"
       @prev-click="prevClick"
       @next-click="nextClick"
       v-if="hackReset"
       ></el-pagination>
    </div>
    <!-- loading组件 -->
    <div class="loading" v-show="!customerList.length>0">
      <loading title="拼命加载中..."></loading>
    </div>
  </div>
</template>

<script>
import {getCustomer, delCustomer, getMajor, getLevel, queryCustomerById} from 'api/customer'
import Loading from 'components/loading/loading'
import customerForm from 'components/customerForm/customerForm'

import moment from 'moment'
import {debounce} from 'common/js/util'
const TABLETOP = 278
export default {
  data () {
    return {
      level: '', // 级别下拉框
      name: '', // 名字搜索框
      major: '', // 专业搜索框
      customerList: [], // 客户列表
      page: 1, // 页码
      pageSize: 6, // 每页条数
      total: 0, // 总页数
      majorList: [], // 专业列表
      levelList: [], // 级别列表
      tip: false, // 专业提示显示
      showTip: '', // 专业提示样式
      addDialogVisible: false, // 增加弹出框
      changeDialogVisible: false,
      form: '', // 表单
      hackReset: true
    }
  },
  created () {
    // 获取每页适合放多少条数据
    this.pageSize = parseInt((document.documentElement.clientHeight - TABLETOP) / 70)
    this.liNum = 0 // 专业搜索栏建议条目
    this.getCustomers()
    this.getMajor()
    this.getLevel()
    // 根据姓名动态搜索(节流)
    this.$watch('name', debounce((newName) => {
      this.major = this.level = ''
      this.page = 1
      this.hackReset = false
      getCustomer(this.page, this.pageSize, newName, null, null).then(res => {
        this.customerList = res.data.list
        this.total = res.data.total
        this.$nextTick(() => {
          this.hackReset = true
        })
      })
    }, 300))
    // 根据专业动态搜索(节流)
    this.$watch('major', debounce((newMajor) => {
      this.name = this.level = ''
      this.page = 1
      this.hackReset = false
      getCustomer(this.page, this.pageSize, null, newMajor, null).then(res => {
        this.customerList = res.data.list
        this.total = res.data.total
        this.$nextTick(() => {
          this.hackReset = true
        })
      })
    }, 300))
  },
  methods: {
    // 查询要修改客户的信息
    alterCustomer (id) {
      queryCustomerById(id).then(res => {
        this.form = res.data
        this.changeDialogVisible = true
      })
    },
    // 上传客户
    postCustomer () {
      this.$refs.addCustomerForm.onSubmit('上传')
    },
    // 修改客户
    postAlterCustomer () {
      this.$refs.alterCustomerForm.alterSubmit('修改')
    },
    // 修改成功后
    alterReload () {
      this.getCustomers()
      this.changeDialogVisible = false
    },
    // 上传成功后
    successReload () {
      this.page = this.total
      this.getCustomers()
      this.addDialogVisible = false
    },
    // 关闭弹框
    handleClose (done) {
      done()
    },
    // 搜索框筛选专业
    filterMajor (list) {
      if (!this.tip) {
        return
      }
      let temp = []
      list.forEach(item => {
        if (item.includes(this.major)) {
          temp.push(item)
        }
      })
      this.liNum = temp.length
      return temp
    },
    // 鼠标进入所属专业搜索框
    focus () {
      this.tip = true
    },
    // 离开
    blur () {
      setTimeout(() => {
        this.tip = false
      }, 200)
    },
    // 获取客户列表
    getCustomers () {
      getCustomer(this.page, this.pageSize, this.name || null, this.major || null, this.level || null).then(res => {
        this.customerList = res.data.list
        this.total = res.data.total
        this.page = res.data.pageNum
      })
    },
    // 获取专业列表
    getMajor () {
      getMajor().then(res => {
        this.majorList = res.data
      })
    },
    // 获取级别
    getLevel () {
      getLevel().then(res => {
        this.levelList = res.data
      })
    },
    // 获取当前页码
    currentChange (currentPage) {
      this.page = currentPage
      this.getCustomers()
    },
    // 上一页
    prevClick (currentPage) {
      this.page = currentPage
      this.getCustomers()
    },
    // 下一页
    nextClick (currentPage) {
      this.page = currentPage
      this.getCustomers()
    },
    // 日期格式化
    dateFormat (ow, column, cellValue, index) {
      return moment(cellValue).format('YYYY-MM-DD')
    },
    // 选择客户级别
    selectLevel () {
      this.name = this.major = ''
      this.level = this.level === '' ? undefined : this.level
      this.page = 1
      this.hackReset = false
      getCustomer(this.page, this.pageSize, null, null, this.level).then(res => {
        this.customerList = res.data.list
        this.total = res.data.total
        this.page = res.data.pageNum
        this.$nextTick(() => {
          this.hackReset = true
        })
      })
    },
    // 删除客户
    deleteById (id) {
      this.$confirm('此操作将永久删除用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delCustomer(id).then(res => {
          this.getCustomers()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          offset: 240
        })
      })
    }
  },
  components: {
    Loading,
    customerForm
  },
  watch: {
    showTip () {
      let liNum = this.liNum
      this.showTip = this.tip ? `height: ${liNum * 36}px` : `height: 0`
    }
  }
}
</script>

<style lang="scss">
@import "common/css/variable.scss";
  .customer {
    margin: 30px 0 0 30px;
    color: $color-theme;
    // position: relative;
    .search {
      margin-top: 25px;
      margin-right: 15px;
      height: 50px;
      border: 1px dashed $color-text-d;
      line-height: 50px;
      color: $color-theme;
      display: flex;
      justify-content: space-around;
      padding-right: 300px;
      .major {
        position: relative;
      }
      .tip {
          position: absolute;
          margin-top: 5px;
          border-radius: 10px;
          overflow: hidden;
          transition: all 0.3s;
          background-color: $color-highlight-background;
          z-index: 20;
          width: 155px;
          top: 40px;
          left: 74px;
          li {
            padding: 10px 15px;
            height: 16px;
            line-height: 16px;
          }
          li:last-child {
            margin-bottom: 5px;
          }
          li:hover {
            cursor: pointer;
            background-color: #222;
          }
      }
    }
    .create {
      margin: 20px 0;
      background-color: $color-theme-d;
      color: #fff;
    }
    .table {
      margin-right: 15px;
       .el-table--enable-row-hover .el-table__body tr:hover>td{
        background-color: #333 !important;
       }

      .page {
        margin-top: 8px;
        text-align: center;
        .el-dialog, .el-pager li {
          color: $color-theme;
          background-color: $color-background;
        }
        .el-pager li.active {
          color: #fff;
        }
      }
      .el-pagination button {
          color: #fff;
          background-color: $color-background;
        }
      .el-table th, .el-table tr {
        background-color: $color-background;
        color: $color-theme;
      }
      .el-table th.gutter {
        display: table-cell !important
      }
      .el-button--text {
        color: $color-theme;
      }
    }
    .loading {
      width: 100px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50px);
    }
  }
</style>



